<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <div>
        <v-app-bar app flat color="#F5DF4D">
            <v-spacer></v-spacer>
            <v-menu style="z-index: 999999999999" offset-y>
                <template v-slot:activator="{ on, attrs }">
                    <v-btn dark icon v-on="on">
                        <v-icon color="black">mdi-dots-vertical</v-icon>
                    </v-btn>
                </template>
                <v-list>
                    <v-list-item exact router: to='/'>홈</v-list-item>
                    <!--<v-list-item @click="showModalFlag()">계좌관리</v-list-item>-->
                    <v-list-item @click="showModal=true">QR이력</v-list-item>
                    <v-list-item @click="showModalFlag()">도움말</v-list-item>
                </v-list>
            </v-menu>
        </v-app-bar>

        <v-container>
            <v-row fluid class="fill-height; align-center">
                <v-col  cols="6" >
                    <!--로고-->
                    <v-col cols="12" class="mt-0 pt-0 al">
                        <v-col class=" align-center ma-0 pa-0">
                            <v-img
                                    alt="KpayLogo"
                                    class="shrink mr-2"
                                    contain
                                    :src="require('../assets/Kpay.svg')"
                                    @click="QrBtn"
                                    transition="scale-transition"
                                    width="120"
                            />
                        </v-col>
                    </v-col>
                    <!--리더기 화면-->
                    <v-col class="px-4">
                        <v-col cols="12" style="background-color: white; min-height: 83vh">
                            <QrReader></QrReader>
                        </v-col>
                    </v-col>
                </v-col>
                <!--QR 결제화면단-->
                <v-col cols="6">
                    <v-col cols="12" style="background-color: white; min-height: 88vh">
                        <QrInfo type="auth"></QrInfo>
                        <QrAuthHistory :showModal="showModal"  @close="showModalState" type="pay"></QrAuthHistory>
                    </v-col>
                </v-col>
            </v-row>
        </v-container>
    </div>

</template>
<script>
    import QrInfo from '../components/QrInfo.vue';
    import QrReader from '../components/QrReader.vue';

    import QrAuthHistory from '../components/QrAuthHistoryModal'
    export default{
        components:{QrInfo,QrReader,QrAuthHistory},
        data(){
            return{
                showModal: false,
            }
        },
        methods:{
            QrBtn(){
                this.$router.push('/');
            },
            showModalState(data){
                this.showModal=data;
             console.log(this.showModal)
            },
            showInsertModalCall(data){
                this.showModal= data
            }
        }
    }
</script>
