<template>
    <div class="modalBg"
         v-show="openModal"
         transition="dialog-bottom-transition"
         scrollable
         style="
        position: fixed;
        z-index: 555;
        top: 5%;
        left: 50%;
        width: 49%;
        height: 100%;"
    >
        <v-card tile>
            <v-toolbar flat dark color="#F5DF4D">
                <v-btn icon dark @click="$emit('close', false)">
                    <v-icon>mdi-close</v-icon>
                </v-btn>
                <v-toolbar-title >QR인증내역</v-toolbar-title>
                <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text style="background-color: #939597">
                <v-col cols="4">
                    <v-card style="background-color: #F5DF4D">
                        <div align="center" class="mx-2">
                            <v-card-title>총 인증인원</v-card-title>
                            <p class="display-1 text--primary" style="padding-bottom: 12px">
                                {{total}}명
                            </p>
                        </div>
                    </v-card>
                </v-col>
                <v-spacer></v-spacer>
                <v-divider class="mb-4 mt-2"></v-divider>

                <v-data-table
                        height="65vh"
                        :headers="headers"
                        :items="desserts"
                        hide-default-footer
                        :page.sync="page"
                        :items-per-page="itemsPerPage"
                        class="elevation-1"
                        @page-count="pageCount = $event"
                        ref="table"
                ></v-data-table>
                <v-pagination
                        color="black"
                        v-model="page"
                        :length="pageCount"
                ></v-pagination>
            </v-card-text>
        </v-card>
    </div>
</template>

<script>
    import { eventBus } from "../main";
    import {QRauthCheckHistory} from '../api/index.js';
    export default {
        components:{},
        props: {
            showModal:{ type:Boolean, default: false },
        },
        data(){
            return{
                openModal: this.showModal,
                page: 1,
                pageCount: 0,
                itemsPerPage: 15,
                headers:[
                    { text: 'NO', width:"5%",align: 'start', sortable: false, value: 'idx',},
                    { text: '예매번호',width:"50%", align: 'start', sortable: false, value: 'ticketNumber',},
                    { text: '좌석번호',width:"30%", align: 'start', sortable: false, value: 'seatNumber',},
                    { text: '인증여부',width:"15%", align: 'start', sortable: false, value: 'authYn',},
                ],
                desserts: [],
                total:"",
            }
        },
        created(){
            eventBus.$on("authList", authList => {
                console.log('이벤트버스',authList)
                this.desserts =[];
                this.desserts = authList.data.content.list;
                this.total = authList.data.content.total;
                // this.$refs.desserts.refresh();
            })
            var vm = this;
            QRauthCheckHistory()
                .then(function(response){
                    console.log(response.data.content)
                    vm.desserts = response.data.content.list;
                    vm.total = response.data.content.total;
                    console.log(vm.desserts)
                })
                .catch(function(error){
                    console.log(error);
                })
        },

        watch: {
            showModal:function(data) {
                this.openModal = this.showModal;
                console.log(this.openModal)
            }
        },
        methods:{
            loading(){

            }
        }
    }
</script>
<style>
    .v-dialog__content .v-dialog__content:active {
        /*width: 50% !important;*/
        width: 50% !important;
    }
</style>
